#wrap {
  flex-wrap : wrap;
  
}

.card {
    background: #fff;
    border-radius: 50px;
    height: 175px;
    margin: 2rem;
    position: relative;
    width: 175px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    text-align: left;
  }

  .card:hover {
    box-shadow: 10px 10px 20px rgb(56, 56, 56);
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  cursor: pointer;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    
    
    

  }
  .card > .img-container > img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    
  }
  
  .card > .content {
    height: 40px;
    font-size: 12px;
  }
  
  .card > .content > ul {
    list-style-type: none;
    
  }
  
  .card > .content > ul > li {
      padding-bottom: 50px;
      position: absolute;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }
  
  
  
 
