.navbar {

    width: 100%;
    position: fixed;
    white-space: nowrap;
    z-index: 99;
    margin-top: -20px;
    padding-top: 0;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    height: 75px;
    
    
}

nav li {
    display: inline-block;
    padding: 26px 0;
    width: 25%;
    color: rgb(216, 191, 46);
    margin-left: 130px;
    font-family: 'Lilita One', cursive;
    
    
  }
  
  ul {
    
    display: flex;
    align-items: center;
    margin-top: 50px;
    
  }
 a {
    color: rgb(216, 191, 46);
    font-size: 20px;
    font-family: 'Lilita One', cursive;
    
    
    
}